import axios from "axios"
import { useState, useEffect } from "react"
import "../css/Order.css"
import { useNavigate } from "react-router-dom"


const Ordercomponents = ({ order }) => {
    const totalPrice = sessionStorage.getItem('price');

    const history = useNavigate();
    const [deliver, setDeliver] = useState({
        dayong: 0,
        total: totalPrice,
        delivery: 1,
        address: ''
    })


    const { dayong, total, delivery, address } = deliver;
    const onChange = (e) => {
        const { value, name } = e.target; //  e.target  name  value  
        setDeliver({
            ...deliver, //  input   
            [name]: value // name    value  
        });
    };


    const handleSubmit = async() => {
        await axios.post('/auth/order', deliver,{headers: {'Authorization': sessionStorage.getItem('token')}})
        .then((res) => {
if(res.status === 201) {
    alert(' .')
    history('/order-detail')
}
        })
    }

    return (
        <div>
            <div className="deliveryinfo">
                <p ></p>


            </div>
            <div className="ordername">

                <p className="orderInfo"> </p>
                <p className="ordercontent">{sessionStorage.user_id}</p>
                {/* {localStorage.getItem(name)} */}
            </div>

            <div className="ordername">

                <label  className="orderInfo" htmlFor=" "> <br/></label><input className="inputcss" type="text" name="address" onChange={onChange} />

            </div>

            <div className="ordername">

            <p  className="orderInfo"></p>
            <p className="ordercontent"></p>

            </div>


            <div className="ordername">

            <p  className="orderInfo"></p>
            <p className="ordercontent">{totalPrice}</p>


            </div>

            <div  className="ordersubmit">
            <button type="submit" onClick={handleSubmit}></button>

            </div>
        </div>

    )
}

export default Ordercomponents