import axios from "axios"
import { Link, useNavigate } from 'react-router-dom';
import { useState, useEffect } from "react"
import "../css/Order.css"



const Packagecomponents = ({ order }) => {
    const totalPrice = sessionStorage.getItem('price');

    const history = useNavigate();

    const [check, setCheck] = useState(false);
    const [deliver, setDeliver] = useState({
        dayong: false,
        total: 0,
        delivery: 0,

        total: totalPrice,

    })


    const { dayong, total, delivery, address } = deliver;

    const onClick = (e) => {

        if (e.target.checked) {
            setCheck(e.target.checked)
            setDeliver({
                ...deliver, //  input   
                dayong: e.target.checked // name    value  
            });

        } else {
            setCheck(e.target.checked)

            setDeliver({
                ...deliver, //  input   
                dayong: e.target.checked // name    value  
            });

        }
    }


    const handleSubmit = async () => {
        await axios.post('/auth/order', deliver, { headers: { 'Authorization': sessionStorage.getItem('token') } })
            .then((res) => {
                if (res.status === 201) {
                    alert(' .')
                    history('/mapinfo')
                }
            })
    }



    const userName = sessionStorage.getItem('user_id')


    return (
        <div>
            <div className="deliveryinfo">
                <p ></p>


            </div>
            <div className="ordername">

                <p className="orderInfo"> </p>
                <p className="ordercontent">{sessionStorage.user_id}</p>
                {/* {localStorage.getItem(name)} */}
            </div>

            <div className="ordername">

                <label className="orderInfo" htmlFor=""> <br /></label>
                <input type="checkbox" name="dayong" checked={check} onChange={onClick} style={{ marginLeft: "5px" }} />
                <label className="ordercontent" htmlFor="">&nbsp;&nbsp; <br /></label>

            </div>

            <div className="ordername">

                <p className="orderInfo"></p>
                <p className="ordercontent"></p>

            </div>


            <div className="ordername">

                <p className="orderInfo"></p>
                <p className="ordercontent">{totalPrice}</p>



            </div>

            <div className="ordersubmit">
                <button type="submit" onClick={handleSubmit}></button>
            </div>
        </div>

    )
}

export default Packagecomponents